import firebase from 'firebase/app';
import 'firebase/auth';

import Vue from "vue";
import Router from "vue-router";
import Meta from 'vue-meta';

Vue.use(Router);
Vue.use(Meta);

const protect = (to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if (requiresAuth && !currentUser) next('login');
  else next();
};

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: () => import('./views/Home.vue'),      
    },
    {
      path: "/portfolio",
      name: "portfolio",
      component: () => import("./views/Portfolio.vue"),
    },
    {
      path: "/portfolio/:title",
      name: "single-work",
      component: () => import("./components/portfolio/PortfolioSingle.vue"),
      props: true
    },
    {
      path: "/blog/:title",
      name: "single-article",
      component: () => import("./components/blog/BlogSingle.vue"),
      props: true
    },
    {
      path: "/blog",
      name: "blog",
      component: () => import("./views/Blog.vue"),
    },
    {
      path: "/new_work",
      name: "new_work",
      component: () => import("./components/portfolio/NewWork.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect
    },
    {
      path: "/new_post",
      name: "new_post",
      component: () => import("./components/blog/NewPost.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect
    },
    {
      path: "/admin",
      name: "admin",
      component: () => import("./views/Admin.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect,
     } ,
    {
      path: "/edit_post",
      name: "edit_post",
      component: () => import("./components/blog/EditPost.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect,
    },
    {
      path: "/edit_draft",
      name: "edit_draft",
      component: () => import("./components/blog/EditDraft.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect
    },
    {
      path: "/edit_work",
      name: "edit_work",
      component: () => import("./components/portfolio/EditWork.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect
    },
    {
      path: "/live_preview",
      name: "live_preview",
      component: () => import("./components/blog/PostPreview.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect
    },
    {
      path: "/work_preview",
      name: "work_preview",
      component: () => import("./components/portfolio/WorkPreview.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect
    },
    {
      path: "/login",
      name: "login",
      component: () => import("./components/auth/Login.vue"),
    },
    {
      path: '/services',
      name: 'services',
      component: () => import("./views/Services.vue"),
    },
    {
      path: '*',
      name: 'empty',
      component: () => import("./views/NotFound.vue")
    }
  ]
});
